.mobile-nav{
    overflow:hidden;
    position: fixed;
    top:0;
    width: 100%;
    z-index:1;
}
.mobile-nav-header{
    height:50px;
    width:100%;
    background-color: #0f1626;
    padding: 8px;
}
.mobile-nav-header .mobile-nav-icon{
    cursor:pointer;
    color:#caebf2;
}
.mobile-nav-title{
    margin-left: 20px;
    color:#caebf2;
    font-weight: bold;
    font-family: italics;
    text-transform: capitalize;
    vertical-align: middle;
    font-size:20px;
}
.mobile-nav-menu .nav-items{
    height:auto;
    width:200px;
    background-color: #0f1626;
    padding:1px;
    padding-bottom:20px;
    border-bottom-right-radius: 10px;
}
@media(min-width:600px){
    .mobile-nav{
        display: none;
    }
}