.testimonial{
    padding:20px;
    margin-left: 130px;
    margin-top: 50px;
    background-color: #ab987a;
    box-shadow:  #0f1626 0px 14px 28px,  #0f1626 0px 10px 10px;
   
}
.testimonial{
    padding:60px 0px;
}
.testimonial h2{
    font-weight: bold;
    font-family:italics;
    color:#0f1626;
}
.testimonial h2{
    font-weight:bold;
    color:#0f1626;
}
.testimonial hr{
    color:#0f1626;
    width: 400px;
    margin: 10px auto;
}
.testimonial .vertical-timeline-element-title{
    color: #0f1626;
}
.testimonial .vertical-timeline-element-subtitle{
    color:#a9a9a9c9;
}

 /* ==========DARK MODE========== */
#dark .testimonial{
    background-color: #0f1626;
    /* box-shadow:  #caebf2 0px 14px 28px,  #caebf2 0px 10px 10px; */

}
#dark .testimonial h2{
    font-weight: bold;
    font-family: italics;
    color:#caebf2;
}
#dark .testimonial hr{
    color: #caebf2;
    width:400px;
    margin:10px auto;
    }
#dark .testimonial .vertical-timeline-element-title{
color:#0f1626;

}
#dark .testimonial .vertical-timeline-element-subtitle{
color:#a9a9a9;
}

/* ==========MOBILE VIEW========== */
@media(max-width:600px){
    .testimonial{
        margin: 0;
        padding: 0;
        margin-top: 50px;    
    } 
    .testimonial hr{
width:280px;    
}
}